import React, { useRef, useState } from "react";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Alert from "react-bootstrap/Alert";
import Image from "react-bootstrap/Image";
import upload from "../../assets/upload.png";
import styles from "../../styles/ListingCreateEditForm.module.css";
import appStyles from "../../App.module.css";
import btnStyles from "../../styles/Button.module.css";
import { useHistory } from "react-router-dom";
import { axiosReq } from "../../api/axiosDefaults";
import Asset from "../../components/Asset";
import { useRedirect } from "../../hooks/useRedirect";
import ListingTextFields from "../../components/createEditFormFields/ListingFormTextFields";
import useUserStatus from "../../hooks/useUserStatus";
import Forbidden403 from "../errors/Forbidden403";

function ListingCreateForm() {
  useRedirect("loggedOut");
  const userStatus = useUserStatus();
  const [selectedAmenities, setSelectedAmenities] = useState([]);
  const [selectedImageIdx, setSelectedImageIdx] = useState(null);

  const [listingData, setListingData] = useState({
    type: "",
    sub_type: "",
    sale_type: "",
    price: "",
    currency: "",
    description: "",
    description_gr: "",
    address_number: "",
    address_street: "",
    address_street_gr: "",
    postcode: "",
    municipality: "",
    municipality_gr: "",
    county: "",
    county_gr: "",
    region: "",
    region_gr: "",
    floor_area: "",
    land_area: "",
    levels: "",
    bedrooms: "",
    wc: "",
    floor: "",
    kitchens: "",
    bathrooms: "",
    living_rooms: "",
    rooms: "",
    power_type: "",
    power_type_gr: "",
    heating_system: "",
    heating_system_gr: "",
    energy_class: "",
    floor_type: "",
    construction_year: "",
    availability: "",
    latitude: "0.0",
    longitude: "0.0",
    service_charge: "",
    renovation_year: "",
    opening_frames: "",
    type_of_glass: "",
    building_coefficient: "",
    cover_coefficient: "",
    length_of_facade: "",
    orientation: "",
    view: "",
    slope: "",
    zone: "",
    distance_from_sea: "",
    distance_from_city: "",
    distance_from_airport: "",
    distance_from_village: "",
    distance_from_port: "",
    images: "",
    uploaded_images: [],
    approved: false,
    featured: false,
    is_first: "",
  });

  const [errors, setErrors] = useState({});
  const imageInput = useRef(null);
  const history = useHistory();

  // Handle image reordering logic
  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
    return result;
  };

  const onDragEnd = (result) => {
    if (!result.destination) return;
    const reorderedImages = reorder(
      listingData.uploaded_images,
      result.source.index,
      result.destination.index
    );
    setListingData({ ...listingData, uploaded_images: reorderedImages });
  };

  const handleChange = (e) => {
    setListingData({
      ...listingData,
      [e.target.name]: e.target.value,
    });
  };

  const handleAmenityChange = (e) => {
    const value = e.target.value;
    setSelectedAmenities((prev) =>
      prev.includes(value)
        ? prev.filter((amenity) => amenity !== value)
        : [...prev, value]
    );
  };

  const handleChangeImage = (e) => {
    if (e.target.files.length) {
      const filesArray = Array.from(e.target.files).map((file) =>
        URL.createObjectURL(file)
      );
      setListingData((prev) => ({
        ...prev,
        uploaded_images: [...prev.uploaded_images, ...filesArray],
      }));
    }
  };

  const handleSelectedImage = (index) => {
    if (index !== null) {
      setSelectedImageIdx(index);
      setListingData({
        ...listingData,
        is_first: index,
      });
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (imageInput.current.files.length === 0) {
      setErrors({ images: ["Please add an image"] });
      return;
    }

    if (
      imageInput.current.files.length !== 0 &&
      (listingData.is_first === undefined ||
        listingData.is_first === null ||
        listingData.is_first === "")
    ) {
      setErrors({ is_first: ["Please select a main image"] });
      return;
    }

    const formData = new FormData();
    // Append listing data fields
    // ...
    // Append image files
    Array.from(imageInput.current.files).forEach((file) => {
      formData.append("uploaded_images", file);
    });

    try {
      const { data } = await axiosReq.post("/listings/", formData);
      history.push(`/listings/${data.id}`);
    } catch (err) {
      setErrors(err.response?.data || {});
    }
  };

  if (userStatus === false) {
    return <Forbidden403 />;
  }

  return (
    <Form onSubmit={handleSubmit}>
      <Row>
        <Col className="py-2 p-0">
          <Container
            className={`${appStyles.Content} ${styles.Container} d-flex flex-column justify-content-center`}
          >
            <Form.Group className="text-center justify-content-between">
              {listingData.uploaded_images.length ? (
                <>
                  <DragDropContext onDragEnd={onDragEnd}>
                    <Droppable droppableId="images" direction="horizontal">
                      {(provided) => (
                        <div
                          className="image-list"
                          {...provided.droppableProps}
                          ref={provided.innerRef}
                          style={{ display: "flex", overflowX: "auto" }}
                        >
                          {listingData.uploaded_images.map((image, index) => (
                            <Draggable
                              key={image}
                              draggableId={image}
                              index={index}
                            >
                              {(provided) => (
                                <div
                                  ref={provided.innerRef}
                                  {...provided.draggableProps}
                                  {...provided.dragHandleProps}
                                  className={`my-2 ${styles.ImageWrapper} ${selectedImageIdx === index
                                    ? styles.SelectedImage
                                    : ""
                                    }`}
                                  onClick={() => handleSelectedImage(index)}
                                  style={{
                                    margin: "0 10px",
                                    cursor: "pointer",
                                  }}
                                >
                                  <Image
                                    src={image}
                                    alt={`upload-${index}`}
                                    rounded
                                    style={{ width: "100px", height: "100px" }}
                                  />
                                </div>
                              )}
                            </Draggable>
                          ))}
                          {provided.placeholder}
                        </div>
                      )}
                    </Droppable>
                  </DragDropContext>
                  <div>
                    <Form.Label
                      className={`${btnStyles.Button} ${btnStyles.Bright} btn`}
                      htmlFor="image-upload"
                    >
                      Change the image
                    </Form.Label>
                  </div>
                </>
              ) : (
                <Form.Label
                  className="d-flex justify-content-center"
                  htmlFor="image-upload"
                >
                  <Asset src={upload} message="Click or tap to upload an image" />
                </Form.Label>
              )}

              <input
                type="file"
                multiple
                id="image-upload"
                accept="image/*"
                onChange={handleChangeImage}
                ref={imageInput}
              />
            </Form.Group>
            {errors?.images?.map((message, idx) => (
              <Alert variant="warning" key={idx}>
                {message}
              </Alert>
            ))}
            {errors?.is_first?.map((message, idx) => (
              <Alert variant="warning" key={idx}>
                {message}
              </Alert>
            ))}

            <div className="d-md-none">
              <ListingTextFields
                listingData={listingData}
                handleChange={handleChange}
                history={history}
                errors={errors}
                create={true}
                handleAmenityChange={handleAmenityChange}
                selectedAmenities={selectedAmenities}
              />
            </div>
          </Container>
        </Col>
      </Row>
      <Row>
        <Col md={12} className="d-none d-md-block p-0 p-md-2">
          <Container fluid className={appStyles.Content}>
            <ListingTextFields
              listingData={listingData}
              handleChange={handleChange}
              history={history}
              errors={errors}
              create={true}
              selectedAmenities={selectedAmenities}
              handleAmenityChange={handleAmenityChange}
            />
          </Container>
        </Col>
      </Row>
    </Form>
  );
}

export default ListingCreateForm;
